import axios from 'axios'
import authHeader from '../../services/auth-header'

export default {
  async fetchLikeLogs(context) {
    const { data } = await axios.get('https://onyxstreaming.com/api/likelog/all')
    return new Promise((resolve, reject) => {
      context.commit('fetchLikeLogsCommit', data)
      resolve()
    })
  },
  async postLike(context, payload) {
    const API_URL = 'https://onyxstreaming.com/api/likelog/store'
    const response = await axios.post(API_URL, {id:payload.id, type: payload.type, user: payload.user})
    console.log(response)
    return new Promise((resolve, reject) => {
      if (response.data.success)
        context.commit('LikePosted', response.data.data)
      resolve()
    })
  }
}
